#root {
    background-image: url("../public/home-background.jpg");
    background-position: top;
    background-size: cover; 
}
.fade-enter {
    opacity: 0;
}
.fade-enter-active {
    opacity: 1;
    transition: opacity 300ms; /* 与timeout匹配 */
}
.fade-exit {
    opacity: 1;
}
.fade-exit-active {
    opacity: 0;
    transition: opacity 300ms;
}